body.pasteleria-canina-y-felina {

    section#first-scroll {
        /* Background moved to critical CSS in header for faster LCP */
        background-position: center;
    }

    img.icon {
        left: -150px;
        top: 47px;
        transform: rotate(-46deg);
        width:120px;

        &.last {
            right: -150px;
            top: 47px;
            transform: rotate(46deg);
            left: unset;
        }
    }
}

/* Fix touch targets for accessibility - Specific elements only */
// Only target the exact problematic elements from PageSpeed report
a[href="https://www.facebook.com/petzschool/"],
a[href="https://www.instagram.com/petz.school/"],
a[href="/contacto/"] {
    min-height: 48px !important;
    min-width: 48px !important;
    padding: 12px !important;
    margin: 8px 4px !important;
    display: inline-block !important;
    text-align: center !important;
    line-height: 1.2 !important;
}

body.mascotas-sanas {
    section#first-scroll {
        background-image: url("../img/landings/mascotas-sanas/mascotas-sanas.webp");
        background-repeat: no-repeat;
        background-size: cover;
    }
}

body.reposteria-de-halloween {

    .spinner > div {
        height: 50px;
        width: 50px;
        background-image: url('../img/landings/reposteria-de-halloween/calabera.png');
    }

    section#first-scroll {
        background-image: url("../img/landings/reposteria-de-halloween/reposteria-de-halloween.webp");
        background-repeat: no-repeat;
        background-size: cover;
    }

    img.icon {
        left: -122px;
        top: 70px;
        transform: rotate(-46deg);

        &.last {
            right: -122px;
            top: 70px;
            transform: rotate(46deg);
            left: unset;
        }
    }

    section.bajada {
        &.principal {
            .sub-header {
                h2 {
                    span {
                        display: block;

                        &:nth-of-type(2) {
                            margin-top: 20px;
                        }
                    }
                }
            }
        }
    }

    section#muestras {
        padding-top: 60px;
    }
}

