body.costura-canina-y-felina {

    section#first-scroll {
        background-image: url("../img/landings/costura-canina-y-felina/costura-canina-y-felina.webp");
        background-repeat: no-repeat;
        background-size: cover;
    }

    section.bajada {
        &.principal {
            .sub-header {

                @media (max-width: $break27) {
                    padding: 25px 6px 25px;
                }

                h2 {
                    span {
                        @media (max-width: $break16) {
                            margin-bottom: 20px;
                        }
                    }
                }
            }
        }
    }

    section#aprender {

        @media (max-width: $break9) {
            padding: 60px 0 45px;
        }

        @media (max-width: $break14) {
            padding: 45px 0 45px;
        }

        @media (max-width: $break27) {
            padding: 30px 0 40px;
        }
    }

    img.icon {
        left: -113px;
        top: 56px;
        transform: rotate(-4deg);
        width: 82px;

        &.last {
            right: -113px;
            top: 56px;
            transform: rotate(90deg);
            left: unset;
        }
    }
}

